<template>
    <div class="modal-content fm-modal-upload">
        <div class="modal-header">
            <h5 class="modal-title">{{ lang.modal.upload.title }}</h5>
            <button type="button" class="close" aria-label="Close" v-on:click="hideModal">
                <span aria-hidden="true">&times;</span>
            </button>
        </div>
        <div class="modal-body">
            <div class="fm-btn-wrapper" v-show="!progressBar">
                <button type="button" class="btn btn-secondary btn-block">
                    {{ lang.btn.uploadSelect }}
                </button>
                <input type="file" multiple name="myfile" v-on:change="selectFiles($event)">
            </div>
            <div class="fm-upload-list" v-if="countFiles">
                <div class="d-flex justify-content-between"
                     v-for="(item, index) in newFiles"
                     v-bind:key="index">
                    <div class="w-75 text-truncate">
                        <i class="far" v-bind:class="mimeToIcon(item.type)"/>
                        <span v-bind:class="[notValidFilesIndexes.includes(index) ? 'text-danger' : '']">
                          {{ item.name }}
                        </span>
                    </div>
                    <div class="text-right">
                        <span v-bind:class="[notValidFilesIndexes.includes(index) ? 'text-danger' : '']">
                            {{ bytesToHuman(item.size) }}
                        </span>
                        <button type="button"
                                class="close"
                                aria-label="Delete"
                                v-on:click="deleteFile(index)"
                                v-bind:title="lang.btn.deleteFile">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                </div>
                <hr>
                <div class="d-flex justify-content-between">
                    <div>
                        <strong>{{ lang.modal.upload.selected }}</strong>
                        <span v-bind:class="[!isValidAllFilesNumber ? 'text-danger' : '']">
                          {{ newFiles.length }}
                        </span>
                    </div>
                    <div class="text-right">
                        <strong>{{ lang.modal.upload.size }}</strong>
                        <span v-bind:class="[!isValidAllFilesSize ? 'text-danger' : '']">
                          {{ allFilesSize }}
                        </span>
                    </div>
                </div>
                <hr>
                <div class="d-flex justify-content-between">
                    <div>
                        <strong>{{ lang.modal.upload.ifExist }}</strong>
                    </div>
                    <div class="form-check form-check-inline">
                        <input class="form-check-input"
                               id="uploadRadio1"
                               type="radio"
                               name="uploadOptions"
                               value="0"
                               checked
                               v-model="overwrite">
                        <label class="form-check-label" for="uploadRadio1">
                            {{ lang.modal.upload.skip }}
                        </label>
                    </div>
                    <div class="form-check form-check-inline">
                        <input class="form-check-input"
                               id="uploadRadio2"
                               type="radio"
                               name="uploadOptions"
                               value="1"
                               checked
                               v-model="overwrite">
                        <label class="form-check-label" for="uploadRadio2">
                            {{ lang.modal.upload.overwrite }}
                        </label>
                    </div>
                </div>
                <hr>
            </div>
            <div v-else><p>{{ lang.modal.upload.noSelected }}</p></div>
            <div class="fm-upload-info">
                <!-- Progress Bar -->
                <div class="progress" v-show="countFiles">
                    <div class="progress-bar progress-bar-striped bg-info" role="progressbar"
                         v-bind:aria-valuenow="progressBar"
                         aria-valuemin="0"
                         aria-valuemax="100"
                         v-bind:style="{width: progressBar + '%' }">
                        {{ progressBar }}%
                    </div>
                </div>
            </div>
            <div class="alert alert-danger" v-if="countFiles && (notValidFilesIndexes.length || !isValidAllFilesSize || !isValidAllFilesNumber)">
                <p v-if="notValidFilesIndexes.length">
                    {{ lang.modal.upload.noAllowFileTypes }} - {{ $store.state.fm.settings.allowFileTypes.join(', ') }}
                </p>
                <p v-if="notValidFilesIndexes.length">
                    {{ lang.modal.upload.noMaxUploadFileSize }} - {{ bytesToHuman($store.state.fm.settings.maxUploadFileSize * 1024) }}
                </p>
                <p v-if="!isValidAllFilesSize">
                    {{ lang.modal.upload.noMaxPostSize }} - {{ bytesToHuman($store.state.fm.settings.maxPostSize * 1024) }}
                </p>
              <p v-if="!isValidAllFilesNumber">
                {{ lang.modal.upload.noMaxFileUploads }} - {{ $store.state.fm.settings.maxFileUploads }}
              </p>
            </div>
        </div>
        <div class="modal-footer">
            <button class="btn"
                    v-bind:class="[countFiles && !notValidFilesIndexes.length && isValidAllFilesSize && isValidAllFilesNumber ? 'btn-info' : 'btn-light']"
                    v-bind:disabled="!countFiles || notValidFilesIndexes.length || !isValidAllFilesSize || !isValidAllFilesNumber"
                    v-on:click="uploadFiles">{{ lang.btn.submit }}
            </button>
            <button class="btn btn-light" v-on:click="hideModal()">{{ lang.btn.cancel }}</button>
        </div>
    </div>
</template>

<script>
import modal from '../mixins/modal';
import translate from '../../../mixins/translate';
import helper from '../../../mixins/helper';

export default {
  name: 'Upload',
  mixins: [modal, translate, helper],
  data() {
    return {
      // selected files
      newFiles: [],

      // overwrite if exists
      overwrite: 0,
    };
  },
  computed: {

    /**
     * Progress bar value - %
     * @returns {number}
     */
    progressBar() {
      return this.$store.state.fm.messages.actionProgress;
    },

    /**
     * Count of files selected for upload
     * @returns {number}
     */
    countFiles() {
      return this.newFiles.length;
    },

    /**
     * Calculate the size of all files
     * @returns {*|string}
     */
    allFilesSize() {
      let size = 0;

      for (let i = 0; i < this.newFiles.length; i += 1) {
        size += this.newFiles[i].size;
      }

      return this.bytesToHuman(size);
    },

    notValidFilesIndexes() {
      const indexes = [];

      for (let i = 0; i < this.newFiles.length; i += 1) {
        const ext = this.newFiles[i].name.split('.').pop();
        if (
          (
            this.$store.state.fm.settings.allowFileTypes.length
            && !this.$store.state.fm.settings.allowFileTypes.includes(ext)
          )
          || (
            this.$store.state.fm.settings.maxUploadFileSize !== null
            && this.newFiles[i].size > this.$store.state.fm.settings.maxUploadFileSize * 1024
          )
        ) {
          indexes.push(i);
        }
      }

      return indexes;
    },

    isValidAllFilesSize() {
      let size = 0;

      for (let i = 0; i < this.newFiles.length; i += 1) {
        size += this.newFiles[i].size;
      }

      return this.$store.state.fm.settings.maxPostSize === null || size <= this.$store.state.fm.settings.maxPostSize * 1024;
    },

    isValidAllFilesNumber() {
      return this.$store.state.fm.settings.maxFileUploads === null || this.newFiles.length <= this.$store.state.fm.settings.maxFileUploads;
    },

      filesFromDrag() {
        this.newFiles = this.$store.state.fm.filesFromDrag
        return this.$store.state.fm.filesFromDrag ;
    },

  },
    watch:{
        // when user drop file to FM we catch this files here
        filesFromDrag(){
            this.newFiles =  this.filesFromDrag
        }
    },
  methods: {
    /**
     * Select file or files
     * @param event
     */
    selectFiles(event) {
      // files selected?
      if (event.target.files.length === 0) {
        // no file selected
        this.newFiles = [];
      } else {
        // we have file or files
        this.newFiles = event.target.files;
      }
    },

    /**
     * Upload new files
     */
    uploadFiles() {
      // if files exists
      if (this.countFiles) {
        // upload files
        this.$store.dispatch('fm/upload', {
          files: this.newFiles,
          overwrite: this.overwrite,
        }).then((response) => {
          // if upload is successful
          if (response.data.result.status === 'success') {
            // close modal window
            this.hideModal();
          }
        });
      }
    },

    /**
     * Delete file
     * @param index
     */
    deleteFile(index) {
      const dt = new DataTransfer();

      for (let i = 0; i < this.newFiles.length; i += 1) {
        if (i !== index) {
          dt.items.add(this.newFiles[i]);
        }
      }

      this.newFiles = dt.files;
    },
  },
};
</script>

<style lang="scss">
    .fm-modal-upload {

        .fm-btn-wrapper {
            position: relative;
            overflow: hidden;
            padding-bottom: 6px;
            margin-bottom: 0.6rem;
        }

        .fm-btn-wrapper input[type=file] {
            font-size: 100px;
            position: absolute;
            left: 0;
            top: 0;
            opacity: 0;
            cursor: pointer;
        }

        .fm-upload-list .far {
            padding-right: 0.5rem;
        }

        .fm-upload-list .form-check-inline {
            margin-right: 0;
        }

        .fm-upload-info > .progress {
            margin-bottom: 1rem;
        }
    }
</style>
